import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Spinner } from '@vkontakte/vkui';

import styles from './Home.module.css';
import SearchForm from '../../containers/organisms/SearchForm';
import './HomePanelIn.css';

const Home = ({ id, isInitialized }) => (
  <Panel id={id} theme="white">
    <div className={styles.Home}>
      <div className={styles.Content}>
        <div className={styles.Header}>Doc Online</div>
        <div className={styles.Subheader}>
          Бесплатный сервис по поиску врачей
        </div>
        {isInitialized ? <SearchForm /> : <Spinner size="large" />}
      </div>
    </div>
  </Panel>
);

Home.propTypes = {
  id: PropTypes.string.isRequired,
  isInitialized: PropTypes.bool.isRequired
};

export default Home;
